<template>
	<div class="hello">
		<h1>{{ msg }}</h1>
		<h2>Essential Linkss</h2>
		<ul v-if="list.length" class="plist">
			<li v-for="item in list"><router-link :to="{ name: 'p', params: { id: item.id }}">{{ item.name }}</router-link></li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'HelloWorld',
		data () {
			return {
				msg: 'Welcome to Your Vue.js is Projct list',
				list: [
					{ id: '646865', name: '求道者的', time: '88565466' },
					{ id: '646865', name: '求道者的', time: '88565466' },
					{ id: '646865', name: '求道者的', time: '88565466' },
					{ id: '646865', name: '求道者的', time: '88565466' },
					{ id: '646865', name: '求道者的', time: '88565466' },
					{ id: '646865', name: '求道者的', time: '88565466' },
				]
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}
</style>



